
































































































.panel
  color: white
  max-width: 960px
  padding-top: 10px
  .form-group
    padding: 10px
  .form-control
    width: 100%
    background-color: transparent
    border: 1px solid white
    color: white
    min-height: 40px
  label
    display: block
    margin-bottom: 10px
    user-select: none

.download
  display: flex

.download-button
  cursor: pointer
  height: 40px
  background-color: transparent
  color: white
  border: 1px solid
  padding: 0 20px
  font-size: 16px
  &:hover
    background-color: rgba(47, 47, 47, 0.72)

@media screen and (min-width: 768px)
  .panel
    padding-left: 30px



