.panel {
  color: #fff;
  max-width: 960px;
  padding-top: 10px;
}
.panel .form-group {
  padding: 10px;
}
.panel .form-control {
  width: 100%;
  background-color: transparent;
  border: 1px solid #fff;
  color: #fff;
  min-height: 40px;
}
.panel label {
  display: block;
  margin-bottom: 10px;
  user-select: none;
}
.download {
  display: flex;
}
.download-button {
  cursor: pointer;
  height: 40px;
  background-color: transparent;
  color: #fff;
  border: 1px solid;
  padding: 0 20px;
  font-size: 16px;
}
.download-button:hover {
  background-color: rgba(47,47,47,0.72);
}
@media screen and (min-width: 768px) {
  .panel {
    padding-left: 30px;
  }
}
